/* ---------------------------------------------------
   GLOBAL + PAGE LAYOUT
--------------------------------------------------- */

body {
  margin: 0;
  font-family: "Inter", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
    'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: #f3f5f7;
  color: #333;
}

.app-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 1.5rem;
  text-align: center;
}

/* ---------------------------------------------------
   HEADER (St. T’s Cardinal Red Gradient)
--------------------------------------------------- */

.app-header {
  font-size: 2rem;
  font-weight: 800;
  margin-bottom: 1.25rem;

  background: linear-gradient(90deg, #C41E3A, #E64141);
  -webkit-background-clip: text;
  color: transparent;

  text-transform: uppercase;
  letter-spacing: 1.5px;
}

/* ---------------------------------------------------
   SEARCH BOX
--------------------------------------------------- */

.search-bar {
  display: block;
  width: 100%;
  max-width: 420px;
  margin: 0 auto 1.5rem auto;
  padding: 0.75rem 1rem;
  font-size: 1rem;

  border: 1px solid #d1d1d1;
  border-radius: 10px;
  background: white;

  box-shadow: 0 1px 4px rgba(0,0,0,0.08);
}

/* ---------------------------------------------------
   TABLE CONTAINER – TRUE CENTERING FIX
--------------------------------------------------- */

.table-container {
  display: flex;
  justify-content: center;
  width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  padding-bottom: 16px;
}

/* ---------------------------------------------------
   CARD-STYLE TABLE
--------------------------------------------------- */

.leaderboard-table {
  width: max-content;
  margin: 0 auto;
  border-collapse: collapse;
  background: white;
  border-radius: 14px;
  overflow: hidden;

  box-shadow: 0 4px 14px rgba(0,0,0,0.09);
}

/* ---------------------------------------------------
   TABLE HEADER
--------------------------------------------------- */

.leaderboard-table th {
  background-color: #C41E3A;
  color: white;
  padding: 12px 16px;
  font-size: 0.95rem;

  text-align: left;
  white-space: nowrap;
  cursor: pointer;

  position: sticky;
  top: 0;
  z-index: 10;
}

/* Sort arrow animation */
.sort-arrow {
  animation: sort-wiggle 0.4s ease-in-out;
}

@keyframes sort-wiggle {
  0% { transform: translateY(0); }
  50% { transform: translateY(-3px); }
  100% { transform: translateY(0); }
}

/* ---------------------------------------------------
   TABLE BODY
--------------------------------------------------- */

.leaderboard-table td {
  padding: 12px 16px;
  font-size: 0.92rem;
  border-bottom: 1px solid #e5e7eb;
  white-space: nowrap;
}

.leaderboard-table tr:hover {
  background-color: #fff2f4;
  transition: 0.2s ease-in-out;
}

/* ---------------------------------------------------
   MSR COLOR CODING (Cardinal Red Theme)
--------------------------------------------------- */

.msr-high {
  color: #C41E3A;  /* Cardinal Red */
  font-weight: 800;
}

.msr-mid {
  color: #d67b00; /* Strong amber */
  font-weight: 600;
}

.msr-low {
  color: #888;
  font-weight: 600;
}

/* ---------------------------------------------------
   NO MOBILE CARDS — CLEAN SINGLE VIEW
--------------------------------------------------- */

.mobile-only {
  display: none !important;
}





















